*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
}

body {
  margin: 0 auto;
  color: #444444;
  background-color: #ffffff;
  font-family: "Lato", "Arial", sans-serif;
  overflow-wrap: anywhere;
}

img {
  max-width: 100%;
  height: auto;
}

.button {
  background-color: #68b738;
  font-family: "Oswald", "Arial", sans-serif;
  line-height: 20px;
  font-size: 20px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  border: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff") format("woff"), url("../fonts/lato/Lato-Regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff") format("woff"), url("../fonts/oswald/oswaldregular.woff2") format("woff2");
  font-display: swap;
}
.main-header__container {
  padding: 0 20px;
}
@media screen and (min-width: 768px) {
  .main-header__container {
    padding: 0 30px;
  }
}
@media screen and (min-width: 1220px) {
  .main-header__container {
    max-width: 1220px;
    padding: 0;
    margin: 0 auto;
  }
}

.page {
  height: 100%;
  font-size: 14px;
  line-height: 18px;
  font-family: "Lato", "Arial", sans-serif;
}

.page__body {
  min-height: 100%;
  margin: 0;
}

.main-header {
  color: #444444;
}
@media (min-width: 1220px) {
  .main-header--index {
    min-height: 694px;
  }
  .main-header--index::before {
    content: "";
    position: absolute;
    width: 50%;
    min-height: 694px;
    background-color: rgba(104, 183, 56, 0.85);
    z-index: -1;
    right: 0;
  }
  .main-header--index::after {
    content: "";
    position: absolute;
    width: 50%;
    right: 0;
    top: 0;
    height: 694px;
    background-image: url("../img/index-background-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-position: left top;
    background-size: cover;
    z-index: -2;
  }
}
@media (min-width: 1220px) and (min-resolution: 2dppx) {
  .main-header--index::after {
    background-image: url("../img/index-background-desktop@2x.jpg");
    background-size: cover;
  }
}

.main-header__nav-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.main-header__burger-menu {
  border: 0;
  background-color: transparent;
  display: block;
}

@media (min-width: 768px) {
  .main-header__burger-menu {
    display: none;
  }
}
.main-header__logo {
  display: block;
}

@media (min-width: 768px) {
  .main-header__logo-image {
    width: 174px;
    height: 50px;
    margin: 0;
  }
}
@media (min-width: 1220px) {
  .main-header__logo-image {
    width: 202px;
    height: 59px;
    margin: 0;
  }
}
.main-header__logo-image:hover, .main-header__logo-image:active {
  opacity: 0.6;
}
.main-header__logo-image:active {
  opacity: 0.3;
}

@media (min-width: 768px) {
  .main-header__text-logo-img {
    display: none;
  }
}

.main-header__svg--open {
  width: 24px;
  height: 24px;
}

.main-nav {
  font-size: 20px;
  line-height: 20px;
  position: relative;
  padding-top: 12px;
}
@media (min-width: 768px) {
  .main-nav {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    padding-top: 24px;
  }
}
@media (min-width: 1220px) {
  .main-nav {
    padding-top: 54px;
    padding-bottom: 7px;
  }
}

.site-list {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  flex-wrap: wrap;
  flex-direction: column;
}
@media (min-width: 768px) {
