.site-list__item {
  border-top: 1px solid #e6e6e6;
  padding-top: 22px;
  padding-bottom: 24px;
}
@media (min-width: 768px) {
  .site-list__item {
    margin: 0;
    padding: 0;
    border-top: none;
  }
}

.site-list__link {
  color: #000000;
  text-decoration: none;
  display: flex;
  justify-content: center;
  font-family: "Oswald", "Arial", sans-serif;
}
@media (min-width: 768px) {
  .site-list__link {
    font-size: 18px;
    text-transform: uppercase;
    color: #000000;
    line-height: 24px;
    padding-bottom: 10px;
  }
}
@media (min-width: 1220px) {
  .site-list__link {
    color: #ffffff;
    font-size: 20px;
    line-height: 30px;
  }
}
@media (min-width: 768px) {
  .site-list__link--index {
    border-bottom: 2px solid #68b738;
  }
}
@media (min-width: 1220px) {
  .site-list__link--index {
    border-bottom: 2px solid #ffffff;
  }
}
.site-list__link:hover {
  opacity: 0.6;
}
.site-list__link:focus {
  opacity: 0.3;
}
.site-list__link:active {
  border-bottom: 2px solid #ffffff;
}

.main-hero {
  position: relative;
  margin: 0 0 20px;
}
.main-hero::before {
  content: "";
  position: absolute;
  width: 100%;
  min-height: 302px;
  background-color: rgba(104, 183, 56, 0.85);
  z-index: -1;
}
@media (min-width: 768px) {
  .main-hero::before {
    display: none;
  }
}
.main-hero::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 302px;
  background-image: url("../img/index-background-mobile@1x.jpg");
  background-position: top center;
  background-repeat: no-repeat;
  background-size: inherit;
  z-index: -2;
}
@media (min-resolution: 2dppx) {
  .main-hero::after {
    background-image: url("../img/index-background-mobile@2x.jpg");
    background-size: contain;
  }
}
@media (min-width: 768px) {
  .main-hero::after {
    display: none;
  }
}

@media (min-width: 768px) {
  .main-hero__wrapper {
    display: grid;
    grid-template-rows: 30% 10% 10%;
    min-width: 768px;
    padding: 0 30px;
  }
}
@media (min-width: 1220px) {
  .main-hero__wrapper {
    display: grid;
    grid-template-columns: 45% 50%;
    grid-template-rows: 20% 25% 15% 10%;
    max-width: 1200px;
    margin: 0 auto;
  }
}
.main-hero__wrapper::before {
  content: "";
  position: absolute;
  width: 100%;
  min-height: 302px;
  background-color: #444444;
  z-index: -2;
}
@media (min-width: 768px) {
  .main-hero__wrapper::before {
    display: none;
  }
}
.main-hero__wrapper::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 302px;
  background-image: url("../img/index-background-mobile@1x.jpg");
  background-position: top center;
  background-repeat: no-repeat;
  background-size: inherit;
  z-index: -2;
}
@media (min-resolution: 2dppx) {
  .main-hero__wrapper::after {
    background-image: url("../img/index-background-mobile@2x.jpg");
    background-size: contain;
  }
}
@media (min-width: 768px) {
  .main-hero__wrapper::after {
    display: none;
  }
}

.main-hero__title {
  color: #ffffff;
  text-align: center;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  margin: 0 auto 25px;
  width: 340px;
  padding-top: 27px;
}
@media (min-width: 768px) {
  .main-hero__title {
    color: #000000;
    text-align: left;
    font-size: 60px;
    line-height: 60px;
    margin: 0;
    grid-row: 1/1;
    margin-left: 87px;
    margin-top: 62px;
    width: 420px;
  }
}
@media (min-width: 1220px) {
  .main-hero__title {
    grid-area: 2/1;
    margin-left: 40px;
    margin-top: -36px;
  }
}

.main-hero__subtitle {
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  color: #ffffff;
  text-align: center;
  font-family: "Oswald", "Arial", sans-serif;
}
@media (min-width: 768px) {
  .main-hero__subtitle {
    color: #000000;
    line-height: 20px;
    font-size: 20px;
    text-align: left;
    grid-area: 2/1;
    margin: 0;
    margin-left: 87px;
    width: 420px;
  }
}
@media (min-width: 1220px) {
  .main-hero__subtitle {
    grid-area: 3/1;
    margin-left: 38px;
    margin-top: 13px;
  }
}

.main-hero__image {
  display: block;
  margin: 0 auto;
  width: 280px;
  height: 270px;
}
@media (min-width: 768px) {
  .main-hero__image {
    width: 709px;
    height: 609px;
  }
}
@media (min-width: 1220px) {
  .main-hero__image {
    width: 552px;
    height: 499px;
    margin: 0;
    transform: translate(-19%, 4%);
  }
}

@media (min-width: 768px) {
  .main-hero__picture {
    grid-area: 3/1/5/-1;
  }
}
@media (min-width: 1220px) {
  .main-hero__picture {
    grid-area: 1/2/4/3;
  }
}

.main-hero__hero-button {
  line-height: 18px;
  font-size: 14px;
  height: 40px;
  margin: 0 20px;
}
@media (min-width: 768px) {
  .main-hero__hero-button {
    grid-area: 3/1;
    margin: 0;
    font-size: 20px;
    line-height: 26px;
    text-transform: uppercase;
    width: 244px;
    min-height: 54px;
    margin-left: 87px;
  }
}
@media (min-width: 1220px) {
  .main-hero__hero-button {
    grid-area: 4/1;
    margin-left: 39px;
    margin-top: 2px;
  }
}
.main-hero__hero-button:hover, .main-hero__hero-button:focus {
  background-color: #5eaa2f;
}
.main-hero__hero-button:active {
  background-color: #5eaa2f;
  color: rgba(255, 255, 255, 0.3);
}

