  margin: 0;
  display: flex;
  flex-direction: column;
  row-gap: 14px;
}

.form__data-container {
  display: flex;
  align-items: center;
  column-gap: 13px;
}

.form__label {
  white-space: nowrap;
}

.form__placeholder {
  border: 1px solid #e7e7e7e7;
  width: 100%;
  padding: 14px 15px;
  margin-bottom: 14px;
}

.form__title {
  color: #000000;
  font-size: 24px;
  line-height: 26px;
  margin: 0;
  margin-top: 26px;
}

.form__personal-container {
  padding: 35px 0 11px;
}

.form__host-container {
  position: relative;
  display: flex;
  align-items: center;
  column-gap: 13px;
  margin-bottom: 14px;
}

.form__input {
  padding: 14px 15px;
  width: 100%;
}

.form__input-svg {
  position: absolute;
  right: 15px;
  display: block;
}

.form__button {
  padding: 14px 0;
  font-size: 16px;
  width: 280px;
  margin-bottom: 21px;
}

.form__required-fields {
  color: #000000;
  font-size: 14px;
  line-height: 16px;
  font-family: "Lato", "Arial", sans-serif;
  text-transform: initial;
  display: flex;
  justify-content: center;
}

.selection-button {
  list-style: none;
  margin: 0;
  padding: 22px 0;
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
  display: flex;
  flex-direction: column;
  gap: 13px;
}

.selection-button__item {
  display: flex;
  column-gap: 15px;
}

.extra-button {
  list-style: none;
  padding: 34px 0;
  margin: 0;
}